import { PageProps, PageRendererProps } from "gatsby";
import * as React from "react";

const HomeHeader = () => <header>archon home header</header>;

const NonHomeHeader = () => <header>archon non home headert</header>;

const HomeFooter = () => <footer>home footer</footer>;

const NonHomeFooter = () => <footer>non home footer</footer>;

export interface ILayout {
  path: string;
  children: React.ReactNode;
}

export const Layout: React.FC<ILayout> = (props) => {
  return (
    <div>
      {props?.path === "/" ? <HomeHeader /> : <NonHomeHeader />}
      <hr />
      <main>{props.children}</main>
      <hr />
      {props?.path === "/" ? <HomeFooter /> : <NonHomeFooter />}
    </div>
  );
};
